@import url(https://fonts.googleapis.com/css?family=Varela + Round);
@import url(
  https://fonts.googleapis.com/css?family=Open + Sans:400,
  300,
  600,
  700
);

html,
body {
  background: #150a2d;
}
.container-fluid {
  font-family: "Varela", sans-serif;
}

.profile {
  margin-top: 15%;
  text-align: center;
}

.name {
  color: #e9ecef;
}

.customTabPane {
  background: #fff;
  box-shadow: 0px 0px 8px 0px;
  margin-top: 1%;
  color: azure;
}

.nav-link {
  cursor: pointer;
  color: darkblue;
}
.divbox {
  background-color: #150a2d;
}
.nav-link.active {
  background: #212529 !important;
  color: white !important;
  border-color: #212529 !important;
  border-radius: 0 !important;
}

.jumbotron {
  background: #212529 !important;
  border-radius: 0 !important;
  margin-bottom: 0 !important;
}

#root {
  background: #352e44;
}

.profileImage {
  box-shadow: 0px 0px 10px 3px;
  border-radius: 50% !important;
  width: 30%;
  border-style: solid;
  margin-bottom: 2%;
}
.socialicon {
  color: #f8f6fa;
  padding: 5px;
}
.skills {
  padding: 5px 10px 16px 11px;
  color: white;
}

.skills > .row {
  margin-top: 3%;
}

.table td {
  color: #dee2e6;
}

.experience,
.education {
  color: #e9ecef;
}

.education > .row,
.experience > .row {
  padding: 1rem;
}

.osContrib {
  color: #e9ecef;
}

.about {
  padding: 26px 23px 26px 26px;
  color: #e9ecef;
}

.about > .row {
  margin-left: 20%;
  padding: 1rem;
}

.githubProjects {
  color: #e9ecef;
  overflow: auto;
}

section {
  width: 100%;
  display: inline-block;
  background: #333;
  height: 50vh;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  text-decoration: underline;
}

.footer-distributed {
  background: #554141;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: bold 16px sans-serif;
  padding: 55px 50px;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
  display: inline-block;
  vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left {
  width: 40%;
}

/* The company logo */

.footer-distributed h3 {
  color: #ffffff;
  font: normal 36px "Open Sans", cursive;
  margin: 0;
}

.footer-distributed h3 span {
  color: lightseagreen;
}

/* Footer links */

.footer-distributed .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;
}

.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}

.footer-distributed .footer-company-name {
  color: #222;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

/* Footer Center */

.footer-distributed .footer-center {
  width: 35%;
}

.footer-distributed .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin: 10px 15px;
  vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope {
  font-size: 17px;
  line-height: 38px;
}

.footer-distributed .footer-center p {
  display: inline-block;
  color: #ffffff;
  font-weight: 400;
  vertical-align: middle;
  margin: 0;
}

.footer-distributed .footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer-distributed .footer-center p a {
  color: lightseagreen;
  text-decoration: none;
}

.footer-distributed .footer-links a:before {
  content: "|";
  font-weight: 300;
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

/* Footer Right */

.footer-distributed .footer-right {
  width: 20%;
}

.footer-distributed .footer-company-about {
  line-height: 20px;
  color: #cbced0;
  font-size: 13px;
  font-weight: normal;
  margin: 0;
}

.footer-distributed .footer-company-about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px;
}

.footer-distributed .footer-icons {
  margin-top: 25px;
}

.footer-distributed .footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;

  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;

  margin-right: 3px;
  margin-bottom: 5px;
}

/* If you don't want the footer to be responsive, remove these media queries */

@media (max-width: 880px) {
  .footer-distributed {
    font: bold 14px sans-serif;
  }

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }

  .footer-distributed .footer-center i {
    margin-left: 0;
  }
}
.scrollable{
  overflow:auto;
}

.DownloadButton {
  background-color: #11ecc3;
  border: none;
  color: #06114a;
  font-size: 14px;
padding: 10px 8px 8px 26px ;
margin: 5px 0px 0px 6px;
  border-radius: 5px;
  cursor: pointer;
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}